<template>
    <layout @addEvent="addEvent" title="管理收货单">
        <ul class="supplier-container has-header">
            <li class="header">
                <input type="text" v-model="projectName" placeholder="输入项目名称进行查询">
                <van-dropdown-menu>
                    <van-dropdown-item v-model="state" :options="stateOption"/>
                </van-dropdown-menu>
                <i @click="btnEvt('query')"></i>
            </li>
            <li class="swipe-cell-item" v-for="it in list" :key="it.id">
                <van-swipe-cell>
                    <template #left>
                        <van-button @click="$router.push({path: '/receiving/detail', query: {id: it.id}})" square type="primary" text="详情" />
                    </template>
                    <van-cell>
                        <div>{{it.projectName}}</div>
                        <span>创建人：{{it.userName}}</span>
                        <span>创建日期：{{it.createTime | dateFormat('yyyy-MM-dd')}}</span>
                        <span class="t">材料：{{it.materialName}} -- {{it.materialWeight}} {{it.materialUnit}}</span>
                        <i v-if="it.state === '作废'" class="cancel-icon"></i>
                    </van-cell>
                    <template #right>
                        <van-button @click="btnEvt('delete', it)" v-show="it.state === '新建'" square type="danger" text="删除"/>
                        <van-button @click="btnEvt('edit', it)" v-show="it.state === '新建'" square type="warning" text="编辑"/>
                        <van-button @click="btnEvt('approve', it)" v-show="it.state === '新建'" square type="primary" text="完成"/>
                        <van-button @click="btnEvt('approve', it)" v-show="it.state === '完成'" square type="primary" text="收款" color="#7232dd"/>
                    </template>
                </van-swipe-cell>
            </li>
            <li v-if="loading" class="more">
                <van-loading color="#1989fa" /> 数据加载中...
            </li>
            <li v-else class="more">
                {{hasMore ? '上滑加载更多数据' : '没有更多数据'}}
            </li>
        </ul>

        <van-popup v-model="collectShow"
            position="bottom"
            :close-on-click-overlay="false"
            :style="{ height: '286px' }">
            <div class="dialog-content">
                <van-form ref="collectForm">
                    <van-field v-model="collectObj.id"
                        name="收货单号"
                        label="收货单号"
                        readonly/>
                    <van-field v-model="collectObj.userName"
                        name="收款人"
                        label="收款人"
                        readonly/>
                    <van-field v-model="collectObj.amount"
                        name="实收金额"
                        label="实收金额"
                        placeholder="请输入实收金额"
                        :rules="[{required: true, message: '输入实收金额'}]"/>
                    <van-field v-model="collectObj.remark"
                        name="备注"
                        label="备注"
                        type="textarea"
                        placeholder="请输入备注"
                        :rules="[{required: true, message: '输入收款备注'}]"/>
                </van-form>
                <div class="btn">
                    <span @click="collectEvent">收款</span>
                    <span @click="collectShow = false" class="c">取消</span>
                </div>
            </div>
        </van-popup>

        <van-popup v-model="finishShow"
            position="bottom"
            :close-on-click-overlay="false"
            :style="{ height: '320px' }">
            <div class="dialog-content">
                <van-form ref="finishNode">
                    <van-field v-model="finishObj.id"
                        name="处理单据"
                        label="处理单据"
                        :rules="[{required: true, message: '输入处理单据'}]"
                        readonly/>
                    <van-field v-model="finishObj.userName"
                        name="完成处理人"
                        label="完成处理人"
                        :rules="[{required: true, message: '输入完成处理人'}]"
                        readonly/>
                    <van-field v-model="finishObj.actualWeight"
                        name="实收数量"
                        label="实收数量"
                        placeholder="输入实收数量"
                        :rules="[{required: true, message: '输入实收数量'}]"/>
                    <van-field v-model="finishObj.salePrice"
                        name="收货单价"
                        label="收货单价"
                        placeholder="输入收货单价"
                        :rules="[{required: true, message: '输入收货单价'}]"/>
                    <van-field v-model="finishObj.remark"
                        name="备注"
                        label="备注"
                        type="textarea"
                        placeholder="请输入备注"
                        :rules="[{required: true, message: '输入支付备注'}]"/>
                </van-form>
                <div class="btn">
                    <span @click="finishEvent">完成</span>
                    <span @click="finishShow = false" class="c">取消</span>
                </div>
            </div>
        </van-popup>
    </layout>
</template>

<script>
    import ListHandle from '../common/ListHandle'
    import { receiveCollectApi, receiveDeleteApi, receiveFinishApi, receiveListApi } from '../../apis/ReceiveApi'
    import Auth from '../Auth'

    export default {
        mixins: [Auth],
        extends: ListHandle,
        data() {
            return {
                collectShow: false,
                collectObj: {
                    id: '',
                    amount: '',
                    userId: '',
                    userName: '',
                    remark: '',
                },
                projectName: '',
                state: '',
                stateOption: [
                    {text: '全部', value: ''},
                    {text: '新建', value: '新建'},
                    {text: '完成', value: '完成'},
                    {text: '作废', value: '作废'}
                ],
                finishShow: false,
                finishObj: {
                    id: '',
                    actualWeight: '',
                    salePrice: '',
                    userId: '',
                    userName: '',
                    remark: ''
                }
            }
        },
        methods: {
            async getList() {
                this.loading = true
                let result = await receiveListApi({
                        page: this.paging.page,
                        size: this.paging.size,
                        projectName: this.projectName,
                        state: this.state
                    })

                if (result.code === 200) {
                    this.list = this.paging.page === 1 ? result.data.rows : this.list.concat(!!result.data ? result.data.rows : [])
                    this.hasMore = !!result.data ? (result.data.total > (this.paging.page * this.paging.size)) : false
                } else {
                    this.paging.page -= 1
                }
                await this.$nextTick()
                this.loading = false
            },
            addEvent() {
                if (!this.handleRole('RECEIVING-01-01', 'C')) return
                this.$router.push('/receiving/edit')
            },
            editEvt({id}) {
                if (!this.handleRole('RECEIVING-01-01', 'U')) return
                this.$router.push({path: '/receiving/edit', query: {id: id}})
            },
            approveEvt(row) {
                if (row.state === '新建') {
                    this.finishObj.id = row.id
                    this.finishObj.actualWeight = 0
                    this.finishObj.salePrice = 0
                    this.finishObj.userId = this.userInfo.id
                    this.finishObj.userName = this.userInfo.name
                    this.finishObj.remark = ''
                    this.finishShow = true
                } else {
                    this.collectObj.id = row.id
                    this.collectObj.amount = 0
                    this.collectObj.userId = this.userInfo.id
                    this.collectObj.userName = this.userInfo.name
                    this.collectObj.remark = ''
                    this.collectShow = true
                }
            },
            async deleteEvt({id}) {
                if (!this.handleRole('RECEIVING-01-01', 'D')) return
                let result = await receiveDeleteApi(id)
                if (result.code === 200) {
                    this.$toast.success('删除收货单成功')
                    this.list.splice(this.list.findIndex(oo => oo.id === id), 1)
                } else {
                    this.$toast.fail('删除收货单失败')
                }
            },
            async finishEvent() {
                if (!this.handleRole('RECEIVING-01-01', 'A')) return
                this.$refs.finishNode.validate()
                    .then(async () => {
                        let result = await receiveFinishApi(this.finishObj)
                        if (result.code === 200) {
                            this.$toast.success('完成收货单成功')
                            this.list = this.list.map(it => {
                                if (it.id === this.finishObj.id) it.state = '完成'
                                return it
                            })
                            this.finishShow = false
                        } else {
                            this.$toast.fail('完成收货单失败')
                        }
                    }).catch(() => {})
            },
            async collectEvent() {
                if (!this.handleRole('RECEIVING-01-01', 'A')) return
                this.$refs.collectForm.validate()
                    .then(async () => {
                        let result = await receiveCollectApi(this.collectObj)
                        if (result.code === 200) {
                            this.$toast.success(`收货单【${this.collectObj.id}】收款成功`)
                            this.collectShow = false
                        } else {
                            this.$toast.fail('收款失败')
                        }
                    }).catch(() => {})
            }
        }
    }
</script>